.footer{
    width: 100%;
    padding:35px 15% 41px 15%;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    background:#EBEBEB;
}
.footer .icon{
    width: 84px;
    margin-right: 56px;
}
.footer .icon>img{
    width: 100%;
}
.footer .code{
    width:100px;
    height:100px;
    background:rgba(255,255,255,1);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footer .code>img{
    margin-bottom: 1rem;
}
.footer .first{
    margin-right: 4.6rem;
}
.footer .code>img{
    width: 100%;
}
.footer .lianxi{
    margin-left: 104px;
    font-size:13px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(1,12,27,1);
    display: flex;
    align-items: center;
    flex-direction: column;
    flex:1;
    display: flex;
    align-items: center;
}
.footer .lianxi>div{
    display: flex;
    align-items: center;
}
.footer .lianxi .light{
    color: #FF334B;
}
@media (max-width: 980px){
    .footer{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding:10px;
        box-sizing: border-box;
    }
    .footer .icon{
        margin-right: 0;
        margin-bottom: 10px;
    }
    .footer .code{
        margin-right: 0;
        margin-bottom: 10px;
    }
    .footer .lianxi{
        margin-left: 0;
        margin-top: 20px;
    }
}
