.banner{
    width: 100%;
    background-image: url('../../icon/banner.png');

    background-size: cover;
    background-repeat: no-repeat;
    padding-top: 155px;
    padding-bottom: 174px;
}
.title_banner{
    font-size:65px;
    font-family:Source Han Serif CN;
    font-weight:bold;
    color:rgba(255,255,255,1);
    line-height:65px;
    margin-left: 246px;
}
.title_banner::after{
    content: ' ';
    display: block;
    width:39px;
    height:5px;
    background:rgba(255,255,255,1);
    margin-top: 25px;
    margin-bottom: 20px;
}
.mess{
    font-size:21px;
    font-family:Source Han Serif CN;
    font-weight:bold;
    color:rgba(255,255,255,1);
    line-height:21px;
    margin-left: 246px;
}
.actricle{
    width:662px;
    margin:0 auto;
    text-align: center;
    font-size:13px;
    font-family:Source Han Sans CN;
    font-weight:400;
    color:rgba(24,24,24,1);
    line-height:21px;
}
.radioList{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-top: 59px;
    padding:0 20%;
    margin-bottom: 93px;
}
.photo{
    width:100%;

    background-image: url("../../icon/back.png");
    background-size: cover;
    background-repeat: no-repeat;
    padding: 46px 20% 44px 20%;

}
.warm{
    font-size:2.2rem;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(255,255,255,1);
    margin-bottom: 1px;

}
.nuannuan{
    font-size:34px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(255,255,255,1);
    margin-top: 0;

}
.zhili{
    font-size:17px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(255,255,255,1);
}
.right{
    width: 100%;
}
.message_right{
    font-size:17px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(1,12,27,1);
    line-height:21px;
    width: 100%;
}
.content{
    display: flex;
    align-items: center;
    width: 100%;
    padding:0 20%;
}
.content .left_box{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.content .left_box .top{
    display: flex;
    margin-top: 0;
    /*margin-bottom: 42px;*/

}
.content .left_box .top .left{
    width:299px;
    height:168px;

}
.content .left_box .top .left img{
    width:299px;
    height:168px;
}
.content .left_box .top .right{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}
.content .left_box .top .right .top{
    display: flex;
    flex-direction: column;
}
.content .left_box .top .right .top h3{
    width:238px;
    height:37px;
    font-size:17px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(1,12,27,1);
    line-height:21px;
}
.content .left_box .top .right .top>p>span:nth-child(1){
    font-size:10px;
    font-family:Source Han Sans CN;
    font-weight:400;
    color:rgba(204,204,204,1);
}
.content .left_box .top .right .top>p>span:nth-child(2){
    font-size:10px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(255,51,75,1);
}
.content .left_box .top .right .bottom{
    width:282px;
    height:63px;
    font-size:13px;
    font-family:Source Han Sans CN;
    font-weight:400;
    color:rgba(141,141,141,1);
    line-height:17px;

}
.content .right_img{
    width:235px;
    height: 470px;
}
.content .right_img>img{
    width:100%;
}
.party_icon{
    width: 100%;
}
.list_party{
    padding:0 20% 50px 20%;
}
.shinian{
    text-align: center;
    margin-top: 44px;
    margin-bottom: 21px;
    font-size:12px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(1,12,27,1);
}
@media (max-width: 980px) {
    .title_banner{
        margin-left: 0;
        text-align: center;
    }
    .title_banner::after{
        margin:10px auto;
    }
    .mess{
        margin:0;
        text-align: center;
    }
    .actricle{
        text-align: center;
        width: 100%;
        box-sizing: border-box;
        padding:0 10px;
    }
    .radioList{
        display: flex;
        flex-direction: column;
    }
    .radioList .radio{
        margin-bottom:10px;
    }
    .content{
        display: flex;
        flex-direction: column;
        padding:0;
    }
    .content .left_box{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .content .left_box .top{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .list{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        text-align: center;
    }
    .bottom{
        width: 100%;
        margin-top: 30px;
    }
    .content .right_img{
        width:100%;
        height:auto
    }
    .content .right_img>img{
        width:100%;
    }
}
