
.list_news{
    /*display: flex;*/
    /*align-items: flex-start;*/
    /*justify-content: space-between;*/
    margin-top: 15px;
    border-bottom:1px solid #EBEBEB;
    width: 100%;
}
.list_news>.box{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
}
.top_name{
    display: inline-block;
    width:250px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight:500;
    color:rgba(1,12,27,1);
    font-size:17px;
    line-height:21px;
    margin-right: 103px;
    margin-left:0;
}
.bot_name{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.time{
    font-size:10px;
    font-family:Source Han Sans CN;
    font-weight:400;
    color:rgba(204,204,204,1);
    margin-right: 50px;
}

.btn{
    font-size:13px;
    font-family:Source Han Sans CN;
    font-weight:500;

    color:rgba(255,51,75,1);

}
@media (max-width: 980px) {
    .list_news{
        display: flex;
        display: -webkit-flex;

        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 375px;
        margin-bottom: 20px;
    }
    .list_news>.box{
        display: block;
        width: 100%;
    }
    .top_name{
        text-align: center;
        width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin:0;
        padding: 0 10px;
        box-sizing: border-box;
    }
    .bot_name{
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
}
