.radio{
    width:250px;
    height:250px;
    background:rgba(255,255,255,1);
    border:2px solid rgba(149,149,149,1);
    border-radius:50%;
    text-align: center;
}
.radio .unti{
    font-size: 21px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:rgba(255,51,75,1);
    line-height:30px;
    margin-bottom: 18px;
    margin-top: 86px;
}
.radio .num{
    font-size:51px;
    font-family:Source Han Sans CN;
    font-weight:500;
    color:#FF334B;
    line-height:30px;
}
.radio .name{
    font-size:21px;
    font-family:Source Han Sans CN;
    font-weight:400;
    color:rgba(41,41,41,1);
    line-height:30px;
}
